import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

Vue.use(VueRouter)

const routes = [
  {
    // 设置网页开始默认跳转注册页面
    path: '/', // 网页打开第一次默认路由路径就是'/'
    component: () => import('@/views/layout'),
    redirect: '/home', // 会导致路由规则数组再次匹配
    children: [
      {
        // 侧边栏导航，点击会切换路由地址，路由地址考数据请求回来铺设上去的
        // 所以路由规则要配合它保持一致
        path: 'home',
        component: () => import('@/views/home')
      },
      {
        path: 'user-info',
        component: () => import('@/views/user/userInfo.vue')
      },
      {
        path: 'user-avatar',
        component: () => import('@/views/user/userAvatar.vue')
      },
      {
        path: 'user-pwd',
        component: () => import('@/views/user/userPwd.vue')
      },
      {
        path: '/art-cate',
        component: () => import('@/views/article/artCate.vue')
      },
      {
        path: '/art-list',
        component: () => import('@/views/article/artList.vue')
      }
    ]
  },
  {
    path: '/reg',
    component: () => import('@/views/register')
    // webpack提供import函数来路由懒加载导入组件
    // 路由懒加载，就是页面路由路径切换到/reg,才去加载对应组件的代码
    // 好处：让首页加载文件体积更小，打开更快
  },
  {
    path: '/login',
    component: () => import('@/views/login')
  }
]

const router = new VueRouter({
  routes
})

const whiteList = ['/login', '/reg']// 白名单（无需登录就可以访问到路由地址）

// 1. 浏览器第一次打开项目页面，会触发一次全局路由守卫函数
// 全局前置路由守卫
// 2.有token值就证明登录了，没有token值未登录
// 3.next()如果强制切换路由地址，会不会走路由守卫（会）:导致死循环
// 解决死循环：设置一个白名单，放行
router.beforeEach((to, from, next) => {
  const token = store.state.token
  // 你登录了才会正常放行
  if (token) {
    // 你在本地有token值，才去请求用户信息
    // 存在问题：如果你不退出登录，存在token值，切换路由，会请求用户信息
    // 解决方法： !store.state.userInfo.username（用户信息不存在 才会去 请求）
    if (token && !store.state.userInfo.username) {
      store.dispatch('getUserInfoActions')
    }
    next()
  } else {
    // 未登录
    // 数组的方法：判断值是否在数组中出现，出现过原地返回true
    // 如果是白名单上的路径就放行
    if (whiteList.includes(to.path)) {
      // 未登录，可以返回路由地址，则放行(路由全局前置守卫不会再次触发，而是匹配路由表，让组件挂载)
      next()
    } else {
      // next强制切换到登录路径上
      // 再次执行路由守卫，判断是否是白名单上的路径
      next('/login')
    }
  }
})
export default router
