// 基于axios封装网络请求的函数
import axios from 'axios'
import store from '@/store'
import router from '@/router'
import { Message } from 'element-ui'

// 定义一个服务器地址的变量(接口和图片资源所在的服务器地址)
export const baseURL = 'http://big-event-vue-api-t.itheima.net'
// axios.create()创建一个带配置项的自定义axios函数
// myAxios请求的时候，地址baseURL+url，然后去请求后台
const myAxios = axios.create({
  baseURL
})

// 定义请求拦截器
myAxios.interceptors.request.use(function (config) {
  // 请求前会触发一次
  // 它返回的是axios内的源码，config配置对象（要请求后台的参数都在这个对象上）

  // 在发起请求时，统一携带Authorization和token值
  // 判断登录页面和注册页面，vuex里有无token值，而且登录接口和注册接口也不需要携带token（其他页面需要）
  // 只有当登录成功才会token参数，这个时候才会有用户信息和侧边栏信息，才会携带请求头
  if (store.state.token) {
    config.headers.Authorization = store.state.token
  }
  return config
}, function (error) {
  // 请求发起前的代码，如果有异常报错，就会直接进入这里
  // 返回一个拒绝状态的Promise对象（axios留在原地的Promise对象状态就为失败结果为error变量值）
  // 此函数类似于catch函数里的return
  // 口诀：return非Promise对象值，会作为成功的结果，返回给下一个Promise对象(axios留在原地)
  // Promise.reject(error) 原地留下一个Promise对象（状态为失败）
  return Promise.reject(error)
}
)

// 响应拦截器
myAxios.interceptors.response.use(
  response => {
    // 响应http状态码为 2xx 或者3xx时触发成功的回调，形参中的 response 是“成功的结果”
    // return到axios原地Promise对象，作为成功的结果
    return response
  },
  error => {
    // console.dir(error)

    // 响应状态码不是 4xx 5xx 时触发失败的回调，形参中的 error 是“失败的结果”
    // return到axios原地Promise对象，作为失败拒绝的状态
    // 如果那边用try catch 或者catch函数捕获，可以获取到我们传递过去的这个error变量的值

    if (error.response.status === 401) {
      // 本次是响应是token过期了
      // 清除vuex
      store.commit('updateToken', '')
      store.commit('updateUserInfo', {})
      // 切换到登录页面（被动退出登录状态）
      router.push('/login')
      Message.error('用户身份已过期！！！')
    }
    return Promise.reject(error)
  })
// 导出axios自定义封装函数
export default myAxios
